<template>
   <div class="login-bgcolor" :style = "style">
       <div class="login-top">
           <div>
                <van-icon size = "36px" color="rgb(236, 89, 89)" name="arrow-left" />
           </div>
       </div>
       <div class="login-body">
           <div class="login-body-div">
               <div class="login-body-icon">
                   <van-icon name="user-o" color = "rgb(236, 89, 89)" size="28px" />
                </div>
                <div class="login-body-input">
                <input type="text" v-model="mobileNum" class="md-input-bd" placeholder="请输入手机号" >
                </div>
           </div>
           <div class="login-body-div">
               <div class="login-body-icon">
                   <van-icon name="bag-o" color = "rgb(236, 89, 89)" size="28px" />
                </div>
                <div class="login-body-input">
                <input type="password" v-model="pwd" class="md-input-bd" placeholder="请输入手机号" >
                </div>
           </div>
           <div class="login-body-div">
               <div class="login-body-icon">
                   <van-icon name="coupon-o" color = "rgb(236, 89, 89)" size="28px" />
                </div>
                <div class="login-body-input-2">
                <input type="text" v-model="pic" class="md-input-bd" placeholder="请输入图形验证码" >
                </div>
                <div class="picyzm">
                    <div class="mobile-btns" @click = 'getpicYzms'><img :src = 'picYzms' alt=""></div>
                </div>
           </div>
           <div class="login-body-div">
               <button @click = "toLogin"  class="btn-doLogin2">登录</button>

           </div>
                <slot name="tologin"></slot>

       </div>
   </div>
</template>

<script type="text/ecmascript-6">
import qs from 'qs';
const axios = require('axios');
import { Dialog } from 'vant';
import { Toast } from 'vant';
import apiConfig from '../../router/httpConfig'

import loginInput from '../mySelf/loginInput'
import loginInputItem from '../mySelf/loginInput-item'
export default {
   name: '',
   data() {
       return {
          mobileNum:'',
          pwd:'',
          picYzms:'',
          pic:'',
          key:'',
          style:{}
       }
   },
  components: {
     loginInputItem,
     loginInput
  },
  methods:{
     getpicYzms:function(){
      let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        this.style = {
            height:clientHeight+'px'
        }
          axios.get(apiConfig.PictureVerificationCode)
                .then((res)=>{
                    // console.log(res.data.data.url)
                        // this.picYzms = res.data.data.url
                        this.picYzms = res.data.data.url
                        this.key = res.data.data.key
                        
                        // this.key = res.data.data.key
            })

     },
     
     toRegester:function(){
        // this.$router.push({
        //    path:this.$route.path,
        //    query: {demo:'regester'}
        // })
        // console.log(this.$router)
     },
      toLogin:function(){
        //   进行登录并且存储数据到 localstore
            axios.post(apiConfig.GettingTokenAndCell,qs.stringify({
                  mobile:this.mobileNum,
                  password:this.pwd,
                  captcha_code:this.key,
                  captcha_key:this.pic

            }))
            .then((res)=>{
               if(res.data.code == '0'){
                     Toast.success('登录成功!');
                     localStorage.setItem('token',res.data.data.token);
                     localStorage.setItem('expire',res.data.data.expire);
                     localStorage.setItem('id',res.data.data.id);
                    //  console.log(localStorage)
                    //  this.$emit('loginOk','1')
                    this.$router.push('/home')

               }else{
                     Toast.fail('登录失败,请检查账户密码是否正确!');
               }
            })
            .catch((err)=>{
                  Toast.fail('登录失败,请检查账户密码是否正确!');
            })
     }
  },
  mounted(){
     this.getpicYzms()
  }

}
</script>

<style lang="less">
.login-bgcolor{
    width:100%;
    // background: linear-gradient(70% rgba(241, 82, 8, 0.87) , 30% rgb(255, 255, 255) ); 
}
.login-bgcolor .login-top{
    width:100%;
    height:60px;
    margin-top: 30px;
    display:flex;
    align-items: center;

}
.login-body{
    width:100%;
    height:50px;
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    flex-wrap: wrap;

}
.login-body >div{
    margin-top: 25px;
}
.login-body-div{
    width:75%;
    height:45px;
    border: 2px solid rgb(236, 89, 89);
    border-radius:50px;
    display:flex;
    align-items: center;
    padding-left:6%;
}
.login-bgcolor .login-body-input{
    width:70%;
    height:30px;
    // margin-left: 15% !important;
    // border-style:none;
    // outline-color: white;
    
    // border: 1px solid #000;

}
.login-body-input>input{
    width:100%;
    height:30px;
    font-size: 16px;
    border: 1px solid rgb(250, 247, 247);
    background-color: rgba(1,1,1,0);
    padding-left:10px;

}
.login-body-input-2{
    width:50%;
    height:30px;
}
.login-body-input-2>input{
    margin-left: 5%;
    height:30px;
    width:100%;
    font-size: 16px;
    margin-top: -3px;
    border: 1px solid rgb(250, 247, 247);
    background-color: rgba(1,1,1,0);

}
.picyzm{
    width:10%;
}
.login-bgcolor .mobile-btns{
    width:30px;
    margin-left: 80%;
    margin-top: 15px;
}
.mobile-btns>img{
    width:60px;
    margin-top: -40%;
    // border: 1px solid rgb(236, 89, 89);
}
.login-body-div:nth-of-type(4){
    background-color: rgb(236, 89, 89);
    border: 1px solid rgba(1,1,1,0);

}
.login-bgcolor .btn-doLogin2{
    width:100%;
    height:100%;
    margin-left: -4%;
    border-style:none;
    background-color:rgba(1,1,1,0);
    color:white;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 1em;
    padding-left: 1em;
}
.login-bgcolor .toRegester{
    width:300px;
    border-radius: 20px;
    height:45px;
    font-size: 24px;
    background-color: rgb(236, 89, 89);

}
</style>



