<template>
   <div class="addAccountPage">
      <div class="addAccountPage-title">
            <span>请选择所需要添加的账户</span>
      </div>
      <van-cell-group>
         <van-field
            v-model="message"
            label="账户昵称"
            type="textarea"
            placeholder="请输入账户昵称"
            rows="1"
            autosize
         />
      </van-cell-group>
      <van-cell-group>
         <van-field
            v-model="init"
            label="初始金额"
            type="textarea"
            placeholder="请输入初始金额"
            rows="2"
            autosize
         />
      </van-cell-group>
      <van-dropdown-menu>
         <van-dropdown-item v-model="value1" :options="option1" />
         <van-dropdown-item v-model="value2" :options="option2" />
      </van-dropdown-menu>
      <button class="addAccountPage-btn" @click="addAccountPage" v-if = "accShow">
         创建账户
      </button>
      <button class="addAccountPage-btn" @click="noaddAccountPage" v-if = "noaccShow">
            <van-loading type="spinner"></van-loading>         
      </button>
   </div>
</template>

<script type="text/ecmascript-6">
 import qs from 'qs';
 const axios = require('axios');
 import { Toast } from 'vant';
import apiConfig from '../router/httpConfig.js'

export default {
   name: '',
   data() {
       return {
          value:'',
           value1: 1,
            value2: 'a',
            option1: [
            { text: '现金', value: 1 },
            { text: '银行', value: 2 },
            { text: '支付平台', value: 3 },
            { text: '其他', value: 4 }
            ],
            option2: [
            { text: '升序', value: 'a' },
            { text: '降序', value: 'b' },
            ],
            message:'',
            init:'',
            accShow:true,
            noaccShow:false
       }
   },
  components: {},
  methods:{
     addAccountPage:function(){
      //   console.log(localStorage.token)
      //   console.log(this.value1)
      this.accShow = false;
      this.noaccShow = true;
         axios.post(apiConfig.NewAccount+'?token='+localStorage.token,qs.stringify({
                        name:this.message,
                        type:this.value1,
                        initial_balance:this.init,
                        remark:'',
                        sort:10                        
                    }))
                    .then((res)=>{
                       console.log(res)
                       // 在本地内存存储刚刚创建好的 账户id
                       localStorage.setItem('AccountId',res.data.data.id)
                       console.log(localStorage.AccountId)
                     if(res.data.status === true){
                        this.$router.push('/myAccount')
                        Toast('创建成功');
                        this.accShow = true;
                        this.noaccShow = false;
                     }else{
                        Toast(res.data.data)
                        this.accShow = true;
                        this.noaccShow = false;
                     }

                    })
     },
     noaddAccountPage(){
        Toast.loading({
        mask: true,
        message: "网速较慢,请稍等..."
      });
     }
  }

  }

</script>

<style lang="less">
.addAccountPage-title{
   width:100%;
   color:#747474;
   font-size: 24px;
   margin-top: 100px;
   font-weight: bold;
   height:30px;
}

.van-cell-group{
   margin-top: 30px;
}
.addAccountPage-btn{
   width: 200px;
    height: 50px;
    border-radius: 25px;
    color: white;
    font-size: 20px;
    border-style: none;
   //  background-color: orange;
   // background-color: rgba(238, 117, 69, 0.87);
  background-color: rgb(236, 89, 89);

    margin-top: 40px;
}

</style>



